// TableModal.jsx
import React from "react";
import CustomModal from "../../components/Modal/customModal";
import ReusableTable from "../../components/Table/Table";

const TableModal = ({
  show,
  onHide,
  data, 
  columns
}) => {
  return (
    <CustomModal
      show={show}
      onHide={onHide}
    >
      <ReusableTable
        data={data}
        columns={columns}
      />
    </CustomModal>
  );
};

export default TableModal;
