// pages/revisionFloat/dataHelpers.js

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck } from "@fortawesome/free-solid-svg-icons";
import { faXmark } from "@fortawesome/free-solid-svg-icons";

const CheckmarkCell = () => (
  <FontAwesomeIcon icon={faCheck} className="green-icon" />
);

const CrossmarkCell = () => (
  <FontAwesomeIcon icon={faXmark} className="cross-icon" />
);

export const getTableData = () => {
    return [
      {
        EIN: "EIN001",
        Title: "Sample Document A",
        Description: "A test document for validation",
        Type: "Document",
        Revision: "A",
        "Dropped Revision ID": "DR-A-001",
        "Connected Child Revision": "Rev-1",
        "Latest child connected": true,
        "To-Be child connected": "-",
        State: "Released",
        Owner: "Alice",
        "CAD Format": "PDF",
        Collabspace: "Engineering Space",
        relationship: "Derived From",
      },
      {
        EIN: "EIN002",
        Title: "Sample Product B",
        Description: "Second row with product data",
        Type: "Physical Product",
        Revision: "B",
        "Dropped Revision ID": "DR-B-002",
        "Connected Child Revision": "Rev-2",
        "Latest child connected": false,
        "To-Be child connected": "Rev-3",
        State: "In Work",
        Owner: "Bob",
        "CAD Format": "STEP",
        Collabspace: "Manufacturing",
        relationship: "Part Of",
      },
      {
        EIN: "EIN003",
        Title: "Raw Material X",
        Description: "Raw material used in assembly",
        Type: "Raw Material",
        Revision: "C",
        "Dropped Revision ID": "DR-C-003",
        "Connected Child Revision": "Rev-1",
        "Latest child connected": true,
        "To-Be child connected": "-",
        State: "Preliminary",
        Owner: "Charlie",
        "CAD Format": "IGES",
        Collabspace: "Materials",
        relationship: "Contained In",
      }
    ];
  };
  

export const tableColumns = [
  {
    accessorKey: "EIN",
    header: "EIN",
    columnPinning: true,
  },
  { accessorKey: "Title", header: "Title" },
  { accessorKey: "Description", header: "Description" },
  { accessorKey: "Type", header: "Type" },
  { accessorKey: "Revision", header: "Revision" },
  {
    accessorKey: "Connected Child Revision",
    header: "Connected Revision",
  },
  {
    accessorKey: "Latest child connected",
    header: "latest Revision Connected",
    cell: ({ cell }) => {
      const value = cell.getValue();
      // Render a checkmark if true, a cross if false
      return value ? <CheckmarkCell /> : <CrossmarkCell />;
    },
  },
  {
    accessorKey: "To-Be child connected",
    header: "Revision to-be Connected",
  },
  {
    accessorKey: "relationship",
    header: "Relationship",
  },
  { accessorKey: "State", header: "State" },
  { accessorKey: "Owner", header: "Owner" },
  { accessorKey: "CAD Format", header: "CAD Format" },
  { accessorKey: "Collabspace", header: "Collabspace" },
];
